.checkbox {
  align-items: center;
  display: flex;
  width: fit-content;
}

.checkbox__label {
  margin-left: 5px;
  font-size: 15px;
  user-select: none;
}

.checkbox_round {
  width: 24px;
  height: 24px;
  position: relative;
  border-radius: 20%;
  border: 2px solid rgba(0,0,0,0.2);
  transition: all 0.3s ease-in;
}

.checkbox__input {
  appearance: none;
  -webkit-appearance: none;
  outline: none;
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: inherit;
  top: 0;
  left: 0;
  z-index: 2;
  cursor: pointer;
}
.checkbox_round:hover, .checkbox_round:has(.checkbox__input:focus) {
  border-color: rgba(0,0,0,0.4);
}
.checkbox_round:after {
  border: 2px solid black;
  border-top: none;
  border-right: none;
  content: "";
  height: 25%;
  width: 45%;
  position: absolute;
  left: 4.5px;
  top: 5.5px;
  transform: rotate(-45deg);
  transform: scale(0);
  z-index: 1;
  transition: inherit;
  opacity: 0;
}
.checkbox_round:has(.checkbox__input:checked) {
  background-color: #00d478 !important;
  border-color: rgba(0,0,0,0.6) !important;
}
.checkbox_round:has(.checkbox__input:checked):after {
  opacity: 0.6;
  transform: scale(1) rotate(-45deg);
}